var curAudioState = 1; // 1: 

cc.Class({
    extends: cc.Component,
    properties: {
        audioSource: {
            type: cc.AudioSource,
            default: null
        },
        audioRes: {
            default: [],
            type: cc.Sprite,
        }
    },

    /*
        : 
        : UI
        : [
            
        ]
        :[
            
        ]
        : 
    */
    onLoad: function () {
        cc.audioEngine.setMaxWebAudioSize(1024 * 10);
        this.changeTexture();
    },

    changeTexture:function(){
          if (tywx.ado.AudioManager.getIsMute()) {
              curAudioState = 0;
          } else {
              curAudioState = 1;
          }
          
          this.changeButtonTexture();
    },

    /*
         : 
         : 
         : [
             
         ]
         :[
             
         ]
         : 
     */
    controlAudio: function () {

        if (curAudioState == 1) {
            this.pause();
            //tywx.stopMusic();
            tywx.ado.AudioManager.stopMusic();
            curAudioState = 0;
            tywx.ado.AudioManager.setIsMute(true);
        } else if (curAudioState == 0) {
            this.play();
            //tywx.playMusic();
            tywx.ado.AudioManager.setIsMute(false);
            tywx.ado.AudioManager.playMusic(tywx.ado.Configs.MUSICS.BG_MUSIC);
            curAudioState = 1;
        }
        // 
        this.changeButtonTexture();
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    play: function () {
        this.audioSource.play();
    },


    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    pause: function () {
        this.audioSource.pause();
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    stop: function () {
        this.audioSource.stop();
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    resume: function () {
        this.audioSource.resume();
    },


    /*
        : 
        : Button
        : [
            
        ]
        :[
            
        ]
        : 
    */
    changeButtonTexture: function () {
        tywx.LOGE(this.audioRes[0]);
        if (curAudioState == 0) {
            this.audioRes[0].node.active = false;
            this.audioRes[1].node.active = true;
        } else if (curAudioState == 1) {
            this.audioRes[0].node.active = true;
            this.audioRes[1].node.active = false;
        } else {
            tywx.LOGE("");
        }
    }

});
