cc.Class({
    extends: cc.Component,

    properties: {
        djName:{
            default:null,
            type:cc.Label,
        },
        djNumber:{
            default:null,
            type:cc.Label,
        },
        djIcon:{
            default:null,
            type:cc.Sprite,
        },

        cutOne:{
            default:null,
            type:cc.Sprite,
        },


        addTwo:{
            default:null,
            type:cc.Sprite,
        },


        xxIcon:{
            default:null,
            type:cc.Sprite,
        },

        czIcon:{
            default:null,
            type:cc.Sprite,
        },
        xx1Icon:{
            default:null,
            type:cc.Sprite,
        },
        preParent: null,
        
    },

    showCZ:function(){
       this.czIcon.node.active = true;
       this.djName.node.active = false;
    },

     showXX:function(){
        this.xxIcon.node.active = true;
        this.xx1Icon.node.active = true;
        this.djName.node.active = false;
     },

     showCutOne:function(){
        this.cutOne.node.active = true;
        this.djName.node.active = false;
     },

     showAddTwo:function(){
        this.addTwo.node.active = true;
        this.djName.node.active = false;
     },
   
    /*
        : 
        : 
        : [
            data:  type:{}
        ]
        :[
            
        ]
        : 
    */
    setData:function(data){
        this.data = data;
        this.flushView();
    },

      /*
        : 
        : 
        : [
            data:  type:{}
        ]
        :[
            
        ]
        : 
    */
    setPreParent:function(preparent){
        this.preParent = preparent;
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    flushView:function(){
    //    this.flushIcon();
    //    this.flushName();
       this.flushNumber();
       if(this.data.id == 5){
          this.showXX();
       }else if(this.data.id == 6){
          this.showCZ();
       }else if(this.data.id == 2){
          this.showAddTwo();
       }else if(this.data.id == 1){
          this.showCutOne();
       }
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    getData:function(){
        return this.data;
    },
    
    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    showUse: function(){
        if(this.useCallBack){
            this.useCallBack()
        }
    },
   /*
        : 
        : 
        : [
            clickcall:  type:Function
        ]
        :[
            
        ]
        : 
    */
    setUseCall:function(useclickcall){
        this.useCallBack = useclickcall;
    },
    

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    clickCall:function(){
        tywx.LOGE(" = "+JSON.stringify(this.data))
        if(this.clickcallBack){
            this.clickcallBack(this.data,this)
        }
    },
   
    /*
        : 
        : 
        : [
            clickcall:  type:Function
        ]
        :[
            
        ]
        : 
    */
    setClickCall:function(clickcall){
      this.clickcallBack = clickcall;
    },
    
    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    onLoad () {
        // this.data = {};
    },


    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    flushNumber:function(){
        this.djNumber.string = this.data.num;
    },

   /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    flushName:function(){
        this.djName.string = this.data.name;
    },

    /*
        : 
        : 
        : [
            
        ]
        :[
            
        ]
        : 
    */
    flushIcon:function(){
        this.djName.string = this.data.name;
    },
  
 
    
});
