module.exports = function(id, parent){
    // maskID
    this.id     = id;
    // mask
    this.parent = parent;
    // 
    var g       = this.parent.getAllgz()[id];
    // number
    this.num   = g.num;
    // 
    this.x = this.id % 5;
    // 
    this.y = parseInt(this.id / 5);
    // 
    this.from =- 1;
    // 
    this.step = 9999999;

    /*
        : 
        : mask
        : [
           
        ]
        :[
           
        ]
        : 
    */
    this.reset = function(){
        
        var g  = this.parent.getAllgz()[this.id];
        // console.log("ID = "+this.id+g.num);
        this.num = g.num;
        this.x = this.id % 5;
        this.y = parseInt(this.id/5);
        this.from =- 1;
        this.step = 9999999;
    };
    this.showMSG = function(){
        console.log("ID = " + this.id + " num = " + this.num + "  " + this.num + "x = " + this.x + "y " + this.y);
    };

}